.subscription {
  position: relative;
  min-height: 851px;
  padding-top: 120px;
  background-color: $color-ghost-white;
  margin-bottom: 120px;

  @include vp-1199 {
    padding-top: 101px;
    margin-bottom: 102px;
  }

  @include vp-767 {
    padding-top: 65px;
    margin-bottom: 70px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 290px;
    height: 495px;
    bottom: -151px;
    right: 0;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../img/barbells@2x.webp");

    @include vp-1199 {
      bottom: -124px;
      right: 0;
      width: 342px;
      height: 437px;
      background-image: url("../img/barbells-tablet@2x.webp");
    }

    @include vp-767 {
      display: none;
    }
  }

  h2 {
    letter-spacing: 0.03em;
    font-family: $ff-alumni;
    text-align: center;
    font-size: 54px;
    line-height: 65px;
    margin: 0;
    margin-bottom: 18px;

    @include vp-1199 {
      font-size: 42px;
      line-height: 50px;
      padding-right: 13px;
      margin-bottom: 21px;
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 38px;
      padding-right: 10px;
    }
  }
}

.subscription__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subscription__content {
  padding: 30px 0 100px;

  @include vp-1199 {
    padding-bottom: 80px;
  }

  @include vp-767 {
    padding: 20px 0 50px;
  }
}

.subscription__list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 42px;
  list-style: none;
  margin: 0 0 20px;
  padding: 0;

  @include vp-1199 {
    grid-template-columns: minmax(290px, 442px);
    gap: 39px;
  }

  @include vp-767 {
    gap: 35px;
  }
}

.subscription__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $color-default-white;
  padding: 56px 69px;
  min-height: 448px;
  max-width: 360px;
  min-width: 290px;
  z-index: 1;
  overflow-wrap: break-word;
  border: 7px solid $color-transparent;

  @include hover-focus {
    border-image: $border-gradient;
    outline: none;

    a {
      border: none;
      padding: 26px 44px;
      background: $button-gradient;
      color: $color-default-white;
    }
  }

  @include vp-1199 {
    max-width: 100%;
    min-width: 290px;
  }

  @include vp-767 {
    padding: 41px 33px;
    min-height: 384px;
  }

  h4 {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 26px;
    line-height: 29px;

    @include vp-767 {
      font-size: 22px;
      margin-bottom: 6px;
    }
  }

  p {
    margin: 0;
    color: $color-chambrey;
    padding-bottom: 97px;

    @include vp-767 {
      padding-bottom: 79px;
    }
  }

  a {
    font-weight: 700;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.05em;
    border: 2px solid $color-sapphire;
    width: 206px;
    cursor: pointer;
    color: $color-sapphire;

    @include focus {
      outline-color: $color-sapphire;
    }
  }
}

.subscription__min-text {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-weight: 700;
  color: $color-egyptian-blue;
  font-size: 60px;
  padding: 0 44px 71px 0;

  @include vp-767 {
    font-size: 48px;
    padding: 0 33px 55px 0;
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/svg/rouble.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 30px;
    height: 42px;
    top: 16%;
    right: -9%;
    transform: translate(-50%, -50%);

    @include vp-767 {
      width: 24px;
      height: 33px;
      right: -11%;
      top: 17%;
    }
  }
}

.subscription__max-text {
  position: absolute;
  opacity: 0.5;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 100px;
  font-weight: 700;
  color: $color-grey;
  z-index: -1;
  user-select: none;

  @include vp-767 {
    font-size: 80px;
    top: 46%;
  }
}

.subscription__btn {
  display: inline-flex;
  padding: 22px 42px;
  justify-content: center;
  outline-offset: 3px;
  color: $color-default-white;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  transition: opacity $trans-default;

  @include vp-1199 {
    padding: 24px 23px;
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  @include focus {
    outline: 2px solid;
  }
}

.subscription__buttons {
  margin-bottom: 8px;
  width: 100%;
  max-width: 341px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 10px 0;
  gap: 25px 46px;
  justify-content: space-between;
  border-bottom: 2px solid $color-border-grey;

  @include vp-1199 {
    max-width: 333px;
    column-gap: 55px;
    margin-bottom: 14px;
  }

  @include vp-767 {
    column-gap: 45px;
    padding-bottom: 12px;
  }

  button {
    position: relative;
    font-size: 20px;
    line-height: 30px;
    padding: 0;
    user-select: none;
    outline-color: $color-sapphire;
    cursor: pointer;
    border: none;
    background-color: transparent;
    color: $color-sapphire;
    opacity: 0.5;

    @include vp-1199 {
      font-size: 18px;
      line-height: 27px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 24px;
    }

    @include hover-focus {
      opacity: 1;
    }
  }

  button.is-active {
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: $color-crimson;
      left: 0;
      bottom: -12px;
      z-index: 5px;

      @include vp-767 {
        bottom: -14px;
      }
    }
  }
}
